.item {
	font-size: 1rem;
	list-style-type: none;
	padding: 14px 25px;
	border-bottom: 1px solid #fff;
	background: #f0efef;
}
.content {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 1rem;
}
.content span {
	flex: 1;
	order: 1;
}
.content button {
	cursor: pointer;
	background: transparent;
	order: 2;
	outline: none;
	outline: none;
	border: none;
	margin-left: 7px;
}
.textInput {
	
	text-align: left;
	padding: 10px;
	border: 1px solid #dfdfdf;
	outline: none;
}